body,html {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
#root{
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.timeBox{
    font-size: 24px;
    color: #333333;
    position: absolute;
    right: 50px;
    bottom: 30px;
    width: 600px;
    text-align: right;
    transition: all ease .3s;
}
a{
    text-decoration: none;
}
.date{
    margin-right: 20px;
}
.time{
    margin-right: 30px;
    width: 140px;
    display: inline-block;
}
.timeBox:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
}
.searchBox{
    width: 670px;
    height: 50px;
    border-bottom: 1px solid #ddd;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -335px;
    margin-top: -50px;
    transition: all linear .3s;
}
.searchBox:hover{
    box-shadow: 2px 2px 20px rgba(241, 14, 71, 0.6);
    margin-top: -60px;
    background: url(/static/media/872536.891f3630.png) no-repeat cover;
}
.searchInput{
    width: 100%;
    height: 100%;
    outline: none;
    box-sizing: border-box;
    border: none;
    padding: 0 20px;
    color: #333;
    font-size: 16px;
    font-family: 'Microsoft Yahei';
}
.birthday{
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: -200px;
}
img{
    display: block;
    width: 120xp;
    height: 120px;
    border-radius: 6px; 
}
.text{
    position: absolute;
    left: 10px;
    top: 30px;
    color: #ffffff;
}
.wrap{
    width: 400px;
    height: 200px;
    position: absolute;
    right: 0;
    top: 0;
    transition: all linear .5s;
}
.weather{
    border-radius: 4px;
    padding: 6px 10px;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    font-size: 16px;
    transition: all linear .3s;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
}
.weather .weatherIcon{
    width: 30px;
    height: 30px;
    margin-left: 4px;
}
.tips{
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 40px;
    opacity: 0;
    transition: all linear .8s;
}
.wrap:hover .weather{
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
}
.wrap:hover .tips{
    opacity: 1;
}
.App{
    width: 100%;
    height: 100%;
    /* background-image:url('./static/background.jpg'); */
    background-repeat: repeat;
    background-size: 140px 140px; 
    overflow: hidden;
}
