.searchBox{
    width: 670px;
    height: 50px;
    border-bottom: 1px solid #ddd;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -335px;
    margin-top: -50px;
    transition: all linear .3s;
}
.searchBox:hover{
    box-shadow: 2px 2px 20px rgba(241, 14, 71, 0.6);
    margin-top: -60px;
    background: url('../../static/872536.png') no-repeat cover;
}
.searchInput{
    width: 100%;
    height: 100%;
    outline: none;
    box-sizing: border-box;
    border: none;
    padding: 0 20px;
    color: #333;
    font-size: 16px;
    font-family: 'Microsoft Yahei';
}
.birthday{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: -200px;
}
img{
    display: block;
    width: 120xp;
    height: 120px;
    border-radius: 6px; 
}
.text{
    position: absolute;
    left: 10px;
    top: 30px;
    color: #ffffff;
}