.timeBox{
    font-size: 24px;
    color: #333333;
    position: absolute;
    right: 50px;
    bottom: 30px;
    width: 600px;
    text-align: right;
    transition: all ease .3s;
}
a{
    text-decoration: none;
}
.date{
    margin-right: 20px;
}
.time{
    margin-right: 30px;
    width: 140px;
    display: inline-block;
}
.timeBox:hover{
    transform: scale(1.1)
}